

import React, { Component, useEffect, useState } from 'react';
import { connect } from 'react-redux';
import Loader from '../../../components/ui/loader/loader';
import Divider from '../../../components/ui/divider/divider';
import './vendors.css'
//
import * as actionType from '../../../store/actions/action-type'
import getCompany from '../../../services/api/get-company'
// import {getAllProduct} from '../../../services/api/product_catalog';
import { GetToken } from '../../../helpers/token'

const Vendors = (props) => {

    const [gToken, setgToken] = useState(GetToken());
    const [showLoader, setshowLoader] = useState(props.firstRunCompanies);
    const [showPLoader, setshowPLoader] = useState(true);

    const callback = (data) => {
        if (data.status === 200) {
            props.vendorDispatch(data.data);
            setshowLoader(false);
            props.dispatchUpdateFirstRunCompanies(false);
        } else {
            console.log(data.response)
        }
    }

    // const productCallback = (data) => {
    //     if(data.status === 200){
    //         props.getProducts(data.data)
    //         setshowPLoader(false)
    //     }else {
    //         console.log(data.response)
    //     }
    // }

    const companyCallback = (data) => {
        if(data.status === 200){
           props.getCompany(data.data);
           props.showLoader(false);
           props.dispatchUpdateFirstRunCompanies(false);
        }else {
            console.log(data.response);
        }
    }

    useEffect(() => {
        if (gToken) {
            // getAllProduct(productCallback, gToken);
            if (props.firstRunCompanies) {
                getCompany(companyCallback, gToken);
            }
        }
    },[])

    if (showLoader) {
        return (
            <div className="container">
                <Loader />
            </div>
        )
    } else if (props.companies.length === 0) {
        return (
            <div className="vendors-container flex-row u-text-center">
                <h2><b style={{ color: '#bcbcbc' }}>No Data</b></h2>
            </div>
        )
    } else {
        return (
            <div className="vendors-container flex-column">
                <h2><b>Vendor List</b></h2>
                <Divider />
                <div className="vendor-table" style={{ overflowX: 'auto' }}>
                    <table>
                        <thead>
                            <tr>
                                <th>Vendor Comapny</th>
                                <th>Location</th>
                                <th>Machines</th>
                                {/* {
                                        props.products.length > 0 &&(
                                            props.products.map((product) => (
                                                <th key={product._id}>{product.name}</th>
                                            ))
                                        )
                                    } */}
                                <th>Pentagram</th>
                                <th>Halo</th>
                                <th>Ray</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                props.companies.map((vendor) => {
                                    var haloCount = 0, pentagramCount = 0, rayCount = 0;
                                    return (
                                        <tr key={vendor._id}>
                                            <td>{vendor.name}</td>
                                            <td>{vendor.location}</td>
                                            <td className="text-right"><span>{vendor.products.length}</span></td>
                                            {
                                                vendor.products.length > 0 && (
                                                    vendor.products.map((product) => {
                                                        if (product.name === 'Halo') {
                                                            haloCount++
                                                        } else if (product.name === 'Pentagram') {
                                                            pentagramCount++
                                                        } else if (product.name === 'Ray') {
                                                            rayCount++
                                                        }
                                                    })
                                                )
                                            }
                                            <td className="text-right"><span>{haloCount}</span></td>
                                            <td className="text-right"><span>{pentagramCount}</span></td>
                                            <td className="text-right"><span>{rayCount}</span></td>
                                        </tr>
                                    )
                                })
                            }
                        </tbody>
                    </table>
                </div>
            </div>
        )
    }

}

function mapStateToProps(state) {
    return {
        companies: state.Company.company,
        firstRunCompanies: state.Company.firstRun,
        // products: state.Products.products,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        vendorDispatch: (data) => {
            dispatch({
                type: actionType.COMPANY,
                value: data
            })
        },
        // getProducts: (data) => {
        //     dispatch({
        //         type: actionType.PRODUCTS,
        //         value: data
        //     })
        // },
        dispatchUpdateFirstRunCompanies: (data) => {
            dispatch({
                type: actionType.UPDATE_FIRST_RUN_COMPANY,
                value: data
            })
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Vendors)